<template>
  <header class="header">
    <Menu class="header-menu" mode="horizontal" theme="light" :active-name="$route.name">
      <Row>
        <Col :lg="6" :md="9" :sm="11" :xs="18" style="minWidth: 230px">
          <h2 class="logo">我的个人博客 | 技术博客</h2>
        </Col>
        <Col :lg="13" :md="10" :sm="13" :xs="0">
          <Menu-item
            v-for="(item, index) in list"
            :name="item.name"
            :key="index"
            class="header-menu-item"
          >
            <router-link class="header-link" tag="span" :to="item.type">{{item.title}}</router-link>
          </Menu-item>
        </Col>
        <Col :lg="0" :md="0" :sm="0" :xs="6">
          <Dropdown trigger="click" style="margin-left: -40px">
            <a href="javascript:void(0)" class="showButton">
              <Button icon="ios-menu" />
            </a>
            <DropdownMenu slot="list" class="dropdown-menu" placement="bottom-end">
              <DropdownItem v-for="(item, index) in list" :key="index">
                <router-link class="header-link" tag="span" :to="item.type">{{ item.title }}</router-link>
              </DropdownItem>
            </DropdownMenu>
          </Dropdown>
        </Col>
        <Col :lg="5" :md="5" :sm="24" :xs="24" class="padding">
        <Dropdown trigger="custom" :visible="visible" style="margin-left: 20px">
          <Input
            class="search_text"
            search
            placeholder="请输入关键字"
            v-model="values"
            style="width: 200px"
          />
          <DropdownMenu slot="list" class="dropdown-menu search-item-wrapper" placement="bottom-end">
            <DropdownItem v-for="(item, index) in articleList" class="search-link" :key="index">
              <router-link  tag="span" :to="item.link">{{ item.title }}</router-link>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>
        </Col>
      </Row>
    </Menu>
  </header>
</template>

<script>

import axios from "axios";

export default {
  name: "myHeader",

  data() {
    return {
      value: "",
      list: [
        { title: "首页", name: 'home', type: "/" },
        { title: "地图", name: 'map', type: "/map" },
        { title: "关于", name: 'about', type: "/about" },
        { title: "留言", name: 'comments', type: "/comments" }
      ],
      timer: null,
      values: "",
      articleList: [],
      num: 0,
      visible: false
    };
  },

  methods: {
    queryBlog(val) {
      if (val) {
        let url = `/api/blog/queryBlogByKeyword?K=${val}`
        axios(url)
          .then(data => {
            if (data.status === 200) {
              let arr = []
              data.data.data.forEach(item => {
                let temp = {}
                temp.link = `/detail/${item.id}`
                temp.title = item.title
                arr.push(temp)
              })
              this.$set(this, 'articleList', arr)
              console.log(this.articleList)
              this.visible = this.articleList.length ? true : false
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    search(fun, time) {
      clearTimeout(this.timer)
      if (this.values === '') {
        this.visible = false
        return
      }
      this.timer = setTimeout(() => {
        fun(this.values);
      }, time);
    }
  },

  mounted () {
    document.getElementsByTagName('html')[0].onclick = (e) => {
      if (e.target.nodeName != 'INPUT') {
        this.$set(this, 'visible', false)
      }
    }
  },

  watch: {
    values () {
      this.search(this.queryBlog, 500)
    }
  }
};
</script>

<style scoped>
@import "../css/base.css";
.logo {
  display: inline-block;
  float: left;
}
.showButton {
  z-index: 10000;
}
.search-link {
  min-width: 200px;
}
</style>

