<template>
   <div class="login-wrapper">
       <p class="login-title">login</p>
       <i-form :model="formCustom" :rules="ruleCustom" :label-width="80">
           <Form-item label="用户名" prop="user">
               <i-input type="text" v-model="formCustom.user" number></i-input>
           </Form-item>
           <Form-item label="密码" prop="password">
               <i-input type="password" v-model="formCustom.password"></i-input>
           </Form-item>
       </i-form>
           <i-button  type="success" class="login-btn" @click="handleSubmit">提交</i-button>
   </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'login',
        data () {
            const validatePass = (rule, value, callback) => {
                if (value === '') {
                    this.isUse = false
                    callback(new Error('请输入密码'))
                    return
                }
                this.isUse = true
                callback()
            };
            const validateAge = (rule, value, callback) => {
                if (!value) {
                    this.isPass = false
                    return callback(new Error('用户名不能为空'))
                }
                this.isPass = true
                callback()
                // 模拟异步验证效果
                // setTimeout(() => {
                //     if (!Number.isInteger(value)) {
                //         callback(new Error('请输入用户名'));
                //     } else {
                //         if (value < 18) {
                //             callback(new Error('必须年满18岁'));
                //         }
                //     }
                // }, 1000);
            };

            return {
                formCustom: {
                    password: '',
                    user: ''
                },
                ruleCustom: {
                    password: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    user: [
                        { validator: validateAge, trigger: 'blur' }
                    ]
                },
                isUse: false,
                isPass: false
            }
        },
        methods: {
            async handleSubmit () {
                if(!this.isUse || !this.isPass) {
                    this.$Message.error('请输入正确参数')
                    return
                }
                // console.log(this.formCustom)
                let data = await axios.post('/api/blog/admin/login', this.formCustom)
                if (data.status === 200 && data.data.status === 1) {
                    let msg = data.data
                    this.$Message.success(msg.data)
                    this.$router.push('/admin')
                } else {
                    let msg = data.data
                    this.$Message.error(msg.data)
                }
            }
        }
    }
</script>

<style scoped>
    .login-wrapper {
        width: 400px;
        padding: 20px;
        background-color: #7ca4c1;
        border-radius: 18px;
        position: relative;
        /*top: 50%;*/
        left: 50%;
        margin-left: -200px;
        margin-top: 200px;
    }
    .login-wrapper .login-title {
        font-size: 18px;
        padding: 10px 0;
    }
    .login-wrapper .login-btn {
        width: 60%;
    }
</style>
