<template>
	<div class="add-article-wrapper">
		<Row>
			<i-col :span="18">
				<i-input v-model="titleVal" sizle="large" class="add-title" placeholder="请输入文章标题"></i-input>
			</i-col>
			<i-col :span="14"><i-input v-model="tagVal" sizle="large" class="add-title" placeholder="请输入标签用,分隔" /></i-col>
			<i-col :span="6"><i-button type="success" @click="requestArt">发布文章</i-button><i-button @click="cancel" class="empty-btn">取消</i-button></i-col>
		</Row>
		<mavon-editor ref=md :ishljs="true" v-model="contentVal" @imgAdd="handleAddImg" @imgDel="handleDelImg" class="value-wrapper" />
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: 'addArticle',

		data () {
			return {
				titleVal: '',
				tagVal: '',
				contentVal: '',
				articleId: null,
			}
		},

		methods: {
			cancel () {
				this.$router.push('/admin')
			},
			async requestArt () {
				let data = null
				if (this.contentValidation().status) {
					if (this.articleId != null) {
						data = await axios.post('/api/blog/addArticle', {
							type: 'update',
							content: this.contentVal,
							tags: this.tagVal,
							title: this.titleVal,
							bid: this.articleId
						})
						if (data.status === 200) {
							let res = data.data
							if (res.status === 1) {
								this.$Message.success(res.data.msg)
								this.$router.push('/admin')
							} else {
								this.$Message.error(res.data.msg)
							}
						}
					} else {
						data = await axios.post('/api/blog/addArticle', {
							type: 'add',
							content: this.contentVal,
							tags: this.tagVal,
							title: this.titleVal,
							views: 0
						})
						if (data.status === 200) {
							let res = data.data
							if (res.status === 1) {
								this.$Message.success(res.data.msg)
								this.$router.push('/admin')
							} else {
								this.$Message.error(res.data.msg)
							}
						}
					}
					return
				}
				this.$Message.error(this.contentValidation().msg)
				// if (data.status === 200 && data.data.status === 1) {
				// }
			},
			contentValidation () {
				if (this.titleVal.length < 2) {
					return {msg: '标题长度不能小于2', status: false}
				} else if (!this.tagVal) {
					return {msg: '标签不能为空', status: false}
				} else if (this.contentVal.length < 20) {
					return {msg: '你确定有少于20字的文章', status: false}
				}
				console.log('66666666666')
				return {status: true}
			},
			async queryArtById () {
				let data = await axios.get(`/api/blog/queryArtById?bid=${this.articleId}`)
				if (data.status === 200 && data.data.status === 1) {
					let res = data.data.data[0]
					this.titleVal = res.title
					this.tagVal = res.tags
					this.contentVal = res.content
				}
			},

			handleAddImg (pos, $file) {
				// 第一步.将图片上传到服务器.
				var formdata = new FormData();
				formdata.append('image', $file);
				axios({
					url: '/api/blog/addImg',
					method: 'post',
					data: formdata,
					headers: { 'Content-Type': 'multipart/form-data' },
				}).then((url) => {
					console.log(url)
					this.$refs.md.$img2Url(pos, `http://${url.data}`);
				});
			},
			handleDelImg (pos, $file) {
				console.log(pos, $file)
				let imgUrl = pos[0].split('/')[4];
				axios({
					url: `/api/blog/delImg?imgUrl=${imgUrl}`,
					method: 'get'
				}).then(res => {
					console.log(res);
				});
			}
		},

		created () {
			let articleId = this.$route.query.id
			if (articleId) {
				this.articleId = articleId
				this.queryArtById()
			}
			document.title = '写文章'
		},
		async beforeRouteEnter (to, from, next) {
			let data = await axios.get('/api/blog/admin/isLogin')
			console.log(to, from, next)
			if (data.status === 200) {
				if (data.data === 'login') {
					next()
				} else {
					next(vm => {
						vm.$router.push('/admin/login')
					})
				}
			} 

		}
	}
</script>

<style scoped>
	.add-article-wrapper {
		padding: 20px;
	}
	.add-title {
		margin-bottom: 20px;
	}
	.value-wrapper {
		height: 600px;
	}
	.empty-btn {
		margin-left: 20px;
	}
</style>
