    <div>
        <div class="left">
            <div class="about-me">
                <h2>关于我</h2>
                <hr>
                <p>博主阿飞 ，性别男，成都人</p>
                <p>
                    <span>天赋异禀</span>
                    <span>骨骼精奇</span>
                    <span>歪门邪道八段</span>
                    <span>掌握各种餐具吃饭技能</span>
                    <span>以上纯属虚构</span>
                    <span>外行人转行前端</span>
                    <span>自学成才</span>
                </p>
                <p>
                    <span>我的gitHub: <a href="https://github.com/lifei5859" target="view_window">https://github.com/lifei5859</a></span>
                    <span>我的csdn: <a href="https://blog.csdn.net/weixin_43139949" target="view_window">https://blog.csdn.net/weixin_43139949</a></span>
                </p>
                <h2>关于博客</h2>
                <hr>
                <p>
                    <span>2019建站</span>
                    <span>自学的技术通过博客练习</span>
                    <span>一些技术通过博客分享</span>
                    <span>艾玛呀没词了</span>
                    <span>纯原生 > vue </span>
                    <span>自己玩 > 腾讯云服务器 上线</span>
                </p>
             <!--    <p>
                    <span>我的gitHub: <a href="https://github.com/lifei5859">https://github.com/lifei5859</a></span>
                    <span>我的csdn: <a href="https://blog.csdn.net/weixin_43139949">https://blog.csdn.net/weixin_43139949</a></span>
                </p> -->
            </div>
            <comment-wrapper
                    :commentList="commentList"
                    :count="count"
                    :pageSize="pageSize"
                    :page="page"
                    :id="this.article.id"
                    :title="this.article.title"
                    :getPage="getPage"
                    @update-com="updateCom"
            />

        </div>
    </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
import CommentWrapper from '../components/commentWrapper'
export default {
    name: 'about',
    components: {
        CommentWrapper
    },
    data () {
       return {
           article: {
            title: '关于',
            id: -10
           },
           use: '',
           email: '',
           commentContent: '',
           commentCode: '',
           error: '',
           replyUse: '',
           page: 1,
           commentList: [],
           count: null,
           pageSize: 10
       }
    },
    methods: {
        verification () {
            let emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/ ,
                use = this.use,
                com = this.commentContent
            if ( use.length === 0 ) {
                this.error = 'useNull'
                return
            } else if (use.length > 12) {
                this.error = 'useError'
                return
            } else if ( !emailReg.test(this.email) ) {
                this.error = 'emailError'
                return
            } else if (com.length < 8) {
                this.error = 'comError'
                return
            } else if (this.codeObj.text.toLowerCase() !== this.commentCode.toLowerCase()) {
                this.error = 'codeError'
                return
            }

            return true
        },
        // queryComCode () {
        //     axios.get('/api/blog/comment/queryCode').then( data => {
        //         if (data.data.status === 1) {
        //             this.codeObj = data.data.data
        //         }
        //     }).catch( err => {
        //         console.log(err)
        //     })
        // },
        addComment () {
            if (this.verification()) {
                this.error = ''
                axios.post('/api/blog/comment/addComment', {
                    bid: -10,
                    use: this.use,
                    email: this.email,
                    commentContent: this.commentContent,
                    parentUse: this.replyUse
                }).then(data => {
                    if (data.data.status === 'success') {
                        alert(data.data.msg)
                    }
                }).catch(err => {
                    console.log(err)
                })
            }
        },
        getComments () {
            let url = `/api/blog/comment/queryComPage?page=${(this.page - 1)*this.pageSize}&pageSize=${this.pageSize}&bid= -10`
            axios.get(url).then( data => {
                if (data.data.status === 1) {
                    let values = data.data.data
                    let arr = []
                    values.forEach(item => {
                        let temp = {}
                        temp.content = item.comments.replace(/<[^>]+>/g, '')
                        temp.email = item.email
                        temp.use = item['user_name']
                        temp.ctime = moment(item.ctime).format('YYYY年MM月DD日 hh:mm')
                        if (item.parentUse) {
                            temp.parentUse = item.parentUse
                        }
                        arr.push(temp)
                    })
                    this.commentList = arr
                } else {
                    throw new Error('请求失败')
                }
            }, err => {
                console.log(err)
            })
        },
        updateCom () {
            console.log('okokokokoko')
            this.getComments()
        },
        getPage (item) {
            this.page = item
            this.getComments()
        },
        getCount () {
            let url = `/api/blog/comment/queryComCount?bid=-10`
            axios.get(url).then(data => {

                if (data.data.status === 1) {
                    this.count = data.data.data[0].count
                }
            })
        },
        reply (use) {
            this.replyUse = use
        }
    },
    created () {
        // this.getBlog()
        // this.queryComCode()
        this.getCount()
        this.getComments()
        // this.$on('updateCom', function () {
        //     cosnole.log(5555)
        //     this.getComments()
        // })
        document.title = '关于'
    }
}
</script>

