<template>
    <div>
        <div class="admin-title-wrapper">
            <p class="admin-title">文章管理</p>
            <i-button @click="addArt" type="success">添加</i-button>
        </div>
        <i-table :columns="columns" :data="data"></i-table>
        <Page :total="count" class="page-btn" :page-size="pageSize" @on-change="getPage"/>
    </div>
</template>

<script>
    import axios from 'axios'
    import moment from 'moment'
    export default {
        name: 'admin',
        data () {
            return {
                columns: [
                    {
                        title: 'ID',
                        key: 'id'
                    },
                    {
                        title: '标题',
                        key: 'title'
                    },
                    {
                        title: '摘要',
                        key: 'content'
                    },
                    {
                        title: '标签',
                        key: 'tags'
                    },
                    {
                        title: '最近修改时间',
                        key: 'utime'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'success',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.revamp(params.index)
                                        }
                                    }
                                }, '修改'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除'),

                            ]);
                        }
                    }
                ],
                data: [
                    {
                        id: 1,
                        title: '测试标题',
                        content: '这是一个测试这是一个测试这是一个测试这是一个测试这是一个测试这是一个测试',
                        tags: '测试,ceshi'
                    }
                ],
                page: 1,
                pageSize: 10,
                count: 0
            }
        },

        methods: {
            revamp (index) {
                let id = this.data[index].id
                console.log(id)
                this.$router.push({path:'/admin/addArticle', query: {id: id}})
            },
            remove (index) {
                let id = this.data[index].id
                this.$Modal.confirm({
                    title: '确定要删除吗？',
                    content: '<p>永久删除将无法恢复</p>',
                    onOk: async () => {
                        let data = await axios.get(`/api/blog/removeArt?bid=${id}`)
                        if (data.status === 200 && data.data.status === 1) {
                            this.$Message.info(data.data.data.msg)
                            this.getList()
                        }
                    },
                    onCancel: () => {
                        this.$Message.info('已取消删除')
                    }
                });

            },
            async getList () {
                let data = await axios.get(`/api/blog/queryArt?page=${this.page - 1}&pageSize=${this.pageSize}`)
                if (data.status === 200 && data.data.status === 1) {
                    let arr = []
                    let res = data.data.data
                    res.forEach(item => {
                        let temp = {}
                        temp.id = item.id
                        temp.title = item.title
                        temp.utime = moment(item.utime).format('YY-MM-DD hh:mm:ss')
                        temp.content = item.content.replace(/<[^>]+>/g, '').substring(0, 50)+ '...'
                        temp.tags = item.tags
                        arr.push(temp)
                    })
                    this.$set(this, 'data', arr)
                }
            },
            getPage (item) {
                this.page = item
                this.getList()
            },
            getCount () {
                axios.get('/api/blog/queryCount').then(data => {
                    if (data.data.status === 1) {
                        this.count = data.data.data[0].count
                    }
                })
            },
            addArt () {
                this.$router.push({name: 'addArticle'})
            }
        },

        created () {
            this.getList()
            this.getCount()
            document.title = '文章管理'
        },
        async beforeRouteEnter (to, from, next) {
            let data = await axios.get('/api/blog/admin/isLogin')
            console.log(to, from, next)
            if (data.status === 200) {
                if (data.data === 'login') {
                    next()
                } else {
                    next(vm => {
                        vm.$router.push('/admin/login')
                    })
                }
            } 
        }
    }
</script>

<style>
    .admin-title-wrapper {
        display: flex;
        margin: 10px 0;
        justify-content: space-between;
    }
    .admin-title {
        margin: 0 20px;
        font-size: 18px;
    }
    .page-btn {
        text-align: left;
    }
</style>
