<template>
    <div>
      <h2 class="map-header">我的个人博客's SiteMap</h2>
        <main class="map-wrapper">
          <div class="map-nav">
            <router-link to="/home/all">个人博客</router-link> » <a href="#">站点地图</a>
          </div>
          <dl class="map-items-wrapper">
            <dt class="map-items-title">
              <h3>最新文章</h3>
            </dt>
            <dd v-for="(item, index) in article" :key="index"><a @click="addId(item.id, item.link)">{{item.articleTitle}}</a></dd>
          </dl>
          <dl class="map-items-wrapper">
            <dd class="categories">
              <p>我的推荐</p>
              <dl>
                <dd><a href="https://book.douban.com/subject/26351021/" target="view_window">你不知道的javaScrtp</a></dd>
                <dd><a href="https://book.douban.com/subject/26638316/" target="view_window">javaScript忍者密集</a></dd>
              </dl>
            </dd>
            <dd class="categories">
              <p>我的页面</p>
              <dl>
                <dd><a href="https://github.com/lifei5859" target="view_window">我的gitHub</a></dd>
                <dd><a href="https://blog.csdn.net/weixin_43139949" target="view_window">我的csdn</a></dd>
              </dl>
            </dd>
          </dl>
          <div class="footer-nav">
            查看首页博客: <router-link to="/home/all">查看个人博客</router-link>
          </div>
        </main>
      <footer class="map-footer">
        <p><a href="#">Baidu-SiteMap</a>    Latest Update: {{ now }}</p><br>
        <p>Powered by<a href="#"> Baidu SiteMap Generator </a>   © 2019 成都</p>
      </footer>
    </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  name: 'blogMap',

  data () {
    return {
      article: [],
      now: ''
    }
  },

  methods: {
    addId (id, link) {
      this.$eventBus.$emit('addId', id)
      this.$router.push(link)
    },
    getBlogAll () {
      let url = `/api/blog/queryArt?page=0&pageSize=0`
      axios.get(url).then(data => {
        if (data.data.status === 1) {
          let values = data.data.data
          let arr = []
          values.forEach(item => {
            let temp = {}
            temp.articleTitle = item.title
            temp.articleContent = item.content.replace(/<[^>]+>/g, '').substring(0, 235)+ '...'
            temp.tags = item.tags.split(',')
            temp.view = item.views
            temp.ctime = moment(item.ctime).format().split('T')[0]
            temp.link = {
              name: 'detail',
              params: {
                id: item.id
              }
            }
            arr.push(temp)
          })
          this.article = arr
        }
      }).catch(err => {
        console.log(err)
      })
    },
    getDate () {
      let nowDate = moment(Date.now()).format().split('T')[0]
      this.now = nowDate
    }
  },
  
  created() {
    this.getBlogAll()
    this.getDate()
    document.title = '博客Map'
  }
}
</script>

<style scoped>
  @import "../css/map.css";
