<template>
    <div class="left">
        <div class="myBb">
            <p class="myBb-content">
                <span>欢迎灌水交流，别看我长时间不发博，博主可一直都在线~</span>
                <span>友链只交换技术类个人博客。</span>
                <span>不要皮，谢谢。</span>
            </p>
        </div>
        <comment-wrapper
                :commentList="commentList"
                :count="count"
                :pageSize="pageSize"
                :page="page"
                :id="-5"
                :title="this.article.title"
                :getPage="getPage"
                @update-com="updateCom"
        />
    </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
import CommentWrapper from '../components/commentWrapper'
export default {
    name: 'comments',

    components: {
        CommentWrapper
    },

    data () {
       return {
           article: {
            id: -5,
            title: '留言'
           },
           use: '',
           email: '',
           commentContent: '',
           commentCode: '',
           error: '',
           replyUse: '',
           page: 1,
           commentList: [],
           count: null,
           pageSize: 10,
           bid: -5
       }
    },

    methods: {
        verification () {
            let emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/ ,
                use = this.use,
                com = this.commentContent
            if ( use.length === 0 ) {
                this.error = 'useNull'
                return
            } else if (use.length > 12) {
                this.error = 'useError'
                return
            } else if ( !emailReg.test(this.email) ) {
                this.error = 'emailError'
                return
            } else if (com.length < 8) {
                this.error = 'comError'
                return
            } else if (this.codeObj.text.toLowerCase() !== this.commentCode.toLowerCase()) {
                this.error = 'codeError'
                return
            }

            return true
        },
        // queryComCode () {
        //     axios.get('/api/blog/comment/queryCode').then( data => {
        //         if (data.data.status === 1) {
        //             this.codeObj = data.data.data
        //         }
        //     }).catch( err => {
        //         console.log(err)
        //     })
        // },
        getComments () {
            let url = `/api/blog/comment/queryComPage?page=${(this.page - 1)*this.pageSize}&pageSize=${this.pageSize}&bid= -5`
            axios.get(url).then( data => {
                if (data.data.status === 1) {
                    let values = data.data.data
                    let arr = []
                    values.forEach(item => {
                        let temp = {}
                        temp.content = item.comments.replace(/<[^>]+>/g, '')
                        temp.email = item.email
                        temp.use = item['user_name']
                        temp.ctime = moment(item.ctime).format('YYYY年MM月DD日 hh:mm')
                        if (item.parentUse) {
                            temp.parentUse = item.parentUse
                        }
                        arr.push(temp)
                    })
                    this.commentList = arr
                } else {
                    throw new Error('请求失败')
                }
            }, err => {
                console.log(err)
            })
        },
        updateCom () {
            this.getComments()
        },
        getPage (item) {
            this.page = item
            this.getComments()
        },
        getCount () {
            let url = `/api/blog/comment/queryComCount?bid=-5`
            axios.get(url).then(data => {

                if (data.data.status === 1) {
                    this.count = data.data.data[0].count
                }
            })
        },
        reply (use) {
            this.replyUse = use
        }
    },

    created () {
        // this.getBlog()
        // this.queryComCode()
        this.getCount()
        this.getComments()
        document.title = '留言页'
    }
}
</script>

<style scoped>
    @import "../css/commentBook.css";
</style>
